// hero4header
.navigation_section {
  margin: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 234px;
  padding-top: 38.04px;
  padding-left: 147px;
  background: $gradient-7;
  @include mobile {
    width: 390px;
    padding-left: 0;
    gap: 230px;
  }
  &-dark {
    @extend .navigation_section;
    background: #000;
  }
  &-light {
    @extend .navigation_section;
    background: $Background;
  }
  &-purple {
    @extend .navigation_section;
    background: $Highlight-2;
    color: #fff;
  }
  .navbar_menu {
    .list {
      list-style: none;
      display: flex;
      flex-direction: row;
      padding-top: 12px;
      gap: 48px;
      .item {
        align-self: center;
        a {
          font-size: 16px;
          line-height: 19px;
          letter-spacing: 0em;
          text-align: right;
          color: $Headlines;
        }
      }
    }
    @include mobile {
      display: none;
    }
  }
  .navbar_btn {
    display: flex;
    flex-direction: row;
    gap: 10px;
    padding-top: 8px;
    .android_app-btn,
    .ioS_app-btn {
      width: 138px;
      height: 40px;
      border: 2px solid #000;
      box-sizing: border-box;
      &_light {
        @extend .android_app-btn;
        border: 2px solid #000;
        background: #fff;
        color: #000;
      }
      &_purple {
        @extend .android_app-btn;
        border: 2px solid #fff;
        background: $Highlight-2;
        color: #fff;
      }
      &_dark {
        @extend .android_app-btn;
        border: 2px solid #fff;
        background: #000;
        color: #fff;
      }
    }
    .ioS_app-btn {
      width: 105px;
      &_light {
        width: 105px;
        border: 2px solid #000;
        background: #fff;
        color: #000;
      }
      &_purple {
        width: 105px;
      }
    }
    @include mobile {
      display: none;
    }
  }
  .hamburger {
    @include mobile {
      width: 30px;
      height: 30px;
      padding-top: 12px;
    }
    ul {
      padding-inline-start: 0px;
      list-style: none;
    }
    .bar {
      @include mobile {
        width: 20px;
        height: 3px;
        background-color: #000;
      }
      &:nth-child(1) {
        @include mobile {
          width: 35px;
          margin-bottom: 6px;
        }
      }
      &:nth-child(2) {
        @include mobile {
          width: 35px;
          margin-bottom: 6px;
        }
      }
      &:nth-child(3) {
        @include mobile {
          position: relative;
          width: 20px;
          left: 13px;
        }
      }
    }
    .bar-light {
      background-color: #fff !important;
    }
  }
  &-no_bg {
    @extend .navigation_section;
    background: transparent;
  }
}

