// hero 11 light
.hero-navigation-lightmode {
  width: 1440px;
  margin: auto;
  display: flex;
  flex-direction: row;
  gap: 24%;
  .hero-nav-menu-lightmode {
    .hero-nav-list-lightmode {
      list-style: none;
      display: flex;
      flex-direction: row;
      align-self: center;
      gap: 48px;
      .hero-nav-item-lightmode {
        a {
          color: #000;
        }
      }
    }
  }
  .hero-nav-buttons-lightmode{
    padding-left: 10%;
    display: flex;
    flex-direction: row;
    gap: 12px;
  }
}

